/***********************
7. Espanol and Kids Toggle
***********************/

.language-toggle{
  margin-top: 19px;
  text-align: right;
  a{
    color: #fff;
    text-decoration: none;
    padding: 5px 20px 5px 20px;
    background: $lang-toggle;
    font-size: 90%;
    &:hover{
      text-decoration: underline;
      color: #fff;
    }
    &:visited, &:active, &:focus {
      color: #fff;
    }
  }
}
