	padding-left: 10px;
}

.usa-nav-primary button[aria-expanded=false] {
  background-image: url("../images/plus-alt.png");
  background-image: url("../images/plus-alt.svg");
}


.usa-nav-submenu a:visited, .usa-nav-submenu a:active, .usa-nav-submenu a:focus{
  color: #fff;
 }

// UFE-1000 Left nav vs. main nav
//
// USWDS sets 'display:none;' for .usa-nav-submenu[aria-hidden=true] but
// the page renders showing the submenu for a moment before javascript
// sets the aria attribute.
//
// This solution ensures that the submenu stays hidden until the aria
// attribute is set to false.
.usa-nav-submenu:not([aria-hidden=false]) {
  display: none;
}
// End UFE-1000

