.usa-nav-submenu {
	position: relative;
	background-color: $nav-bg;
}
.usa-nav-primary a {
	color: #fff;
	padding-left: 1rem;
}
.usa-nav-primary a:hover {
	padding-left: 1rem;
}
.usa-nav-primary ul li:before {
	content: none;
	display: table-cell;
	padding-right: 0.4em;
}
.usa-nav-submenu::before, .usa-nav-primary button::before {
	background-color: $nav-bg;
	content: '';
	display: block;
	height: 100%;
	position: absolute;
	top: 0px;
	width: 1000%;
	right: 100%;
}
.usa-nav-submenu::after, .usa-nav-primary button::after {
	background-color: $nav-bg;
	content: '';
	display: block;
	height: 100%;
	position: absolute;
	top: 0;
	width: 1000%;
	left: 100%;
}
.usa-nav {
	padding: 0;
	overflow-x: hidden;
	overflow-y: scroll;
	width: 100%;
	border-left: none;
  -webkit-transform: translateX(100%);
  -ms-transform: translateX(100%);
	transform: translateX(100%);
	z-index: 2000000004;
}
.usa-nav ul li:before {
/*content: none;*/
}
.usa-nav-close, .usa-nav-close:hover {
	margin: 0;
	width: auto;
	height: 4rem;
	padding-left: 1rem;
	padding-right: 4rem;
	color: $nav-close;
	background-color: white;
  background-image: url(../images/close.png);
	background-image: url(../images/close.svg);
	background-repeat: no-repeat;
	background-position: right 1rem center;
	background-size: 2rem;
}
.usa-nav-close:hover {
	color: $nav-close;
}
