/***********************
4. downlevel-sg
***********************/
.wotp {
 min-height: auto;
 background: $featurelist;
 padding-top: 20px;
}

.wotp h2{
   color: $whats-on-this-page-text;
   margin: 0 0 0 25px;
   padding: 10px 0 10px 0;
   font-weight: bold;
   font-size: 100%;
}

.wotp ul{
 margin: 0 0 0 0;
 padding: 0 0 20px 55px;
 overflow: auto;
}

 .wotp ul li{
 width: 46%;
 float: left;
 margin: 0 20px 8px 0;
 padding: 5px 0 0 0;
 height: 42px;
 line-height: 18px;
}

#content .wotp ul li{
 line-height: 20px;
}

.alrtcntnr{
 border-bottom: 1px dashed $dark-gray;
}
.alrtbx{
 height: 50px;
 background: $lang-toggle;
}

.alrtbx header h2{
 background: url('../images/Icon_Alert.png') center right 10px;
 background-repeat:no-repeat;
 padding: 10px 34px 10px 25px;
 color:  #fff;
 font-weight: normal;
}

.fea-cntntmddl-img {
      margin: auto auto auto auto;
      width: 100%;
      /*max-width: 586px;*/
}
.fea-cntntmddl-img img, .fea-img {
      width: 100%;
      padding: 0 0 20px 0;
}

.fea-cntntmddl-img img, .feature-img {
      width: 100%;
      /*padding: 10px 20px 10px 20px;*/
}

.fea-img-cont {
 float: right !important;
}

figcaption {
	margin: 0 20px 0 20px;
    font-size: 1.6rem;
    color: $visited-link;
}

figure.fea-cntntmddl-img a {
	text-decoration: none;
}

figure.fea-cntntmddl-img img {
	padding: 0 0 0 0;
}

p.embedTtl {
 margin: 0 0 0 0;
 padding: 0 0 0 0;
 font-weight: bold;
}
.embed{
 margin: 10px auto 0px auto; 
 width: 100%;
 /*max-width: 586px;*/
}
.embed iframe{
  width: 100%;
  height: 400px;
  border: 2px solid #ebe6de;
}

header h3 button {
 /*display: block;*/
 z-index: 9999;
 margin: 0 0 0 0;
 background: none;
 border: none;
 font-size: 90%;
 font-family:  Tahoma, Verdana, Arial, Sans-serif;
}

h3.vidscrpt{
 background: url(../images/Icon_Video_Transcript.png) 0px 20px no-repeat;
 padding: 0 0 10px 25px;
}

header h3 span {
 color: $downlevel-h2;
 font-size: 90%; /*sg - 8/16/16 - changed from 100% to 90%*/
 font-weight: bold;
 padding: 0px 27px 3px 0; /*sg - 8/16/16 - changed top padding from 8px to 0px */
 background: url(../images/Icon_Red_Arrow_Up.png) no-repeat;
 text-decoration: underline;
 cursor: pointer;
 background-position: right bottom;
 width: auto;
 display: inline-block;
}

header h3 span.arrowDwn  {
 padding: 0px 27px 3px 0; /*sg - 8/12/16 - changed top padding from 8px to 0 */
 background: url(../images/Icon_Red_Arrow_Down.png) no-repeat;
 text-decoration: underline;
 cursor: pointer;
 background-position: right bottom;
 width: auto;
 display: inline-block;
 font-size: 90%;
}
.transcript, .infographicdesc {
 margin: 0 0 0 35px;
 border-left: 10px solid $beige;
 display: none;
}
.transcript p, .infographicdesc p {
 padding: 0 0 0 15px;
}

.speaker{
 font-weight: bold;
}

/* css for new transcript code */
// .usa-accordion-bordered .usa-accordion-content {
//     border-bottom: 3px solid $beige;;
//     border-left: 3px solid $beige;;
//     border-right: 3px solid $beige;;
// }

ul li.transcript_img {
	padding-top: 0;
	padding-bottom: 0;
}

@media (max-width: 991px) {
#content ul li.transcript_img {
	padding-top: 0;
	padding-bottom: 0;
  }
}

ul li.transcript_img::before {
	position: absolute;
	content: url(../images/Icon_Video_Transcript.png);
	padding: 12px 0 0 5px;
}

ul li.transcript_img button.usa-accordion-button {
	padding-left: 4rem;
}

// ul li.transcript_img button.usa-accordion-button, ul li.transcript_img button.usa-accordion-button:hover {
// 	color: $blue;
// 	background-color: $beige;
// }

ul li.transcript_img button.usa-accordion-button:hover {
	text-decoration: underline;
}
/* css for new transcript code */
.volver a{
 padding: 0 0 4px 0;
}

.linking-img{
 background-color:$AZ-button;
}

.linktous-logo {
 height: 80px;
}

.linktous-logo-sp {
 max-width: 200px;
 height: 80px;
}
.last{
	text-align: right;
	padding: 0 0 10px 0;
}
#content article ul li, #content article ol li{
    line-height: 1.5;
}

#content article .how-do-i-topic ul li, #content article .how-do-i-topic ol li, #content article ul.topics li, #content article ul.dwnlvl li {
    line-height: 20px;
}

div #sm-share {
    display:flex;
	padding-top: 10px;
	padding-bottom: 20px;
}
div #sm-share * {
    margin-top: auto;
    margin-bottom: auto;
	  padding-top: 5px;
}

div #sm-share a {
	padding: 7px;
	text-decoration: none;
}

@media (min-width: 992px) {

	.wotp {
  		height: auto;
 	}
}

@media (max-width: 991px) {

	#content .wotp ul li{
  		width: 100%;
		height: auto;
		padding: 3px 0 9px 0;
		line-height: 20px;
 	}
}

@media (max-width: 768px) {

	.alrtbx{
 		height: auto;
 	}
}

@media (max-width: 480px) {

	.embed iframe{
		height:200px;
	}

  header h3 span  {
		white-space: nowrap;
	}
	header h3 span.arrowDwn  {
		white-space: nowrap;
	}
	#featurelist-video header h3 span.arrowDwn  {
	font-size: 70%;
	}
}

/************
Bizsearch
***********/

#bizsearch {
    border: 1px solid $contact-center-box-border;
    margin: 0 0 25px 0;
}

#bizsearch h2 {
    font-size: 130%;
    display: block;
    color: $contact-center-box-heading;
    padding: 14px 0 14px 65px;
    margin: 0 auto 0 auto;
    height: auto;
    border-bottom: 1px solid $contact-center-box-border;
    background: $contact-center-box;    
}

#bizsearch.no-icon h2 {
    color: $blue;
    padding: 14px 0 14px 25px;
    background: $contact-center-box;
}

#bizsearch ul {
    margin: 0 0 15px 15px;
}

#bizsearch p {
    padding: 0 10px 0 25px;
}

#bizsearch .usa-search {
    padding: 0 10px 0 25px;
    margin: 0 0 25px 0;
}

@media (max-width: 991px) {

  #bizsearch h2 {
      font-size: 120%;
  }
}

@media (max-width: 379px) {
    #bizsearch h2 {
        font-size: 100%;
        background: #d9d9d9;
        background-size: 50px;
}
}

@media (max-width: 320px) {
    #bizsearch h2 {
        font-size: 90%;
        padding: 14px 0 14px 55px;
        background: #d9d9d9;
        background-size: 45px;
}
}
