
/*******************
New Top Nav Template
********************/

.topic-nav {
	min-height: 8rem;
	margin-bottom: 25px;
	display: flex;
	@media screen and (max-width: 600px) {
	display: block;
	}
	.topic-nav-box1 {
		margin-right:0;
		div {
			background-color: $floating-box;
			color: #fff;
			margin: 0;
			padding-left: 1.3rem;
			width: 100%;
			padding-top: 1%;
			min-height: 8rem;
			@media screen and (max-width: 991px) {
					padding-bottom: 10%;
					padding-top: 10%;
				}
			@media screen and (max-width: 900px) {
					padding-bottom: 13%;
					padding-top: 13%;
				}
			@media screen and (max-width: 600px) {
					padding-bottom: 1%;
					padding-top: 2%;
				}
			span {
				display: inline-block;
				padding-top: 2%;
				padding-bottom: 2%;
				padding-left: 7.5rem;
				padding-right: 5rem;
				list-style-type:none;
				background-repeat: no-repeat;
				background-position: 0% 50%;
				background-size: 6rem;
				@media screen and (max-width: 991px) {
					padding-top: 3.5%;
					padding-bottom: 3.5%;
				}
				@media screen and (max-width: 760px) {
					padding-top: 6%;
					padding-bottom: 6%;
				}
				@media screen and (max-width: 600px) {
					padding-left: 7.5rem;
					padding-right: 2rem;
				}
				p {
					margin-top: 0;
					margin-bottom: 0;
				}
			}
		}
		.topic-nav-cap {
			background-image:url("../images/Topic_Icon_Education_White.svg");
		}
		.topic-nav-flag {
			background-image:url("../images/Topic_Icon_About_USA_White.svg");
		}
