	}
		.how-do-i-topic {
			background-color:$aqua-blue;
			width: 33rem;
			padding-bottom: .5rem;
			padding-top: 2rem;
			padding-left: 1rem;
			@media screen and (max-width: 600px) {
				background-color:$aqua-blue;
				width: auto;
				padding-left: 1rem;
				padding-top: 2rem;
			}
			.how-do-i-topic h2{
				padding-left: 1rem;
				font-size: 2rem;
				@media screen and (max-width: 600px) {
					margin-top: 0;
				}
			}
			ul {
				min-height: 8rem;
				li {
					padding-right: 2px;
					padding-bottom: 5px;
					margin-bottom: 5px;
					a {
						text-decoration: none;
						color: $floating-box;
					}
					&:hover, &:focus, &:active {
						text-decoration: underline;
						box-shadow: none;
					}
					&:visited {
						color: $floating-box;
					}
				}
			}
		}

}
