.usa-nav-primary, #sr-current-page, #sr-previous, #sr-next, #sr-expanded{
  display: none;
}
@media screen and (min-width: 951px) {
  .usa-nav-primary{
    display: block;
  }
  #mobile-nav{
    display: none;
  }
}
.usa-nav{
  -webkit-overflow-scrolling: touch;
}

#mobile-nav{

  // Styles for Hide/Show Logic

  button, a, span{
    display: block;
  }
  ul{
    display: none;
  }
  .has-open-item>ul>li{
    display: none;
    &.open{
      display: block;
      >ul{
        display: block;
      }
    }
  }
  >li>ul{
    display: block;
  }
  button~a, button~span{
    display: none;
  }
  .expanded.open{
    >button{
      //display: none;
    }
    >a, >span{
      display: block;
    }
  }


  // Styles for Design

  padding: 0;
  margin: 0 0 80px;

  button, a, span {
    padding: 2rem;
    margin: 0 0 3px;
    width: 100%;
    text-align: left;
    color: $blue;
    font-weight: normal;
    background: none;
    text-decoration: none;
  }
  .open>button, .open>a, .open>span{
    background-color: white;
  }
  li, ul{
    line-height:normal;
    margin: 0;
    padding:0;
    list-style: none;
  }
  [data-current-page], [data-autogeneratedpage]{
    color: $red;
    font-weight: bold;
  }


  a[aria-describedby*="sr-previous"], a[aria-describedby*="sr-next"]{
    background-image: url(../images/arrow-right.svg);
    background-repeat: no-repeat;
    background-position-x: calc(100% - 1rem);
    background-position-y: center;
    background-size: 40px;
    padding-right: 4rem;
  }
  .open>a[aria-describedby*="sr-previous"]{
    &:after {
      content: none;
    }
    background-image: url(../images/arrow-left.svg);
    padding-right: 2rem;
  }


  button, a, span {
    padding-left:3rem;
  }
  .open>a{
    background-position-x: -0.5rem;
  }
  ul{
    button, a, span {
      padding-left:5rem;
    }
    .open>a{
      background-position-x: 1.5rem;
    }
    ul{
      button, a, span {
        padding-left:7rem;
      }
      .open>a{
        background-position-x: 3.5rem;
      }
      ul{
        button, a, span {
          padding-left:9rem;
        }
        ul{
           a, span {
            padding-left: 11rem;
          }
        }
      }
    }
  }
}