/***********************
7. atoz
***********************/

.hiddentext {
  display: none;
}

.az-box {
  padding: 30px;
  border: solid 1px lightgray;
  max-width: 700px;
  margin: auto;
}
.az-list {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40px, 1fr));
  grid-gap: 15px;
}
.az-page h2 {
  color: $AZ-button;
}

.fs {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 40px;
  margin-bottom: -8px;
}

.fed-group {
  display: flex;
  margin: 30px 0 0;
  flex-shrink: 1;
  flex-grow: 1;
  max-width: 300px;
  min-width: 250px;
}

#federal-search {
  background-color: #154285;
  border: none;
  color: white;
}

input#federal-search {
      padding: 1rem 2.7em;
}

.fedsrch-container input {
  width: 100%;
  clear: both;
}
.fedsrch-container .fed-group button[type="submit"],
.fedsrch-container .usa-search-submit{
  background-color: #154285;
  background-image: url(../images/search.svg);
}

.fedsrch-container {
  width: 100%;
  clear: both;

  ::placeholder {
    color: white;
    opacity: 1; /* Firefox */
  }

  :-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: white;
  }

  ::-ms-input-placeholder { /* Microsoft Edge */
  color: white;
  }
}
@media screen and (min-width: 951px){
  [role="federal-search"] {
  width: 35rem;
    margin: 4rem auto 4rem auto;
  }
}

.fedsrch {
  display: flex;
  flex-wrap: wrap;
  justify-content:center;
  padding: 0 0 10px 0;
}
input#fed-search-small{
  width: 100%;
	background-color:#154285;
	margin-right: 3px;
	color: white;
}
  .fed-search.fed-search-small [type="submit"],
  .fed-search.fed-search-small .fed-search-submit{
	background-image: url(../images/search.svg);
  }
.fed-group {
  display: flex;
  margin: 30px 0;
  flex-shrink: 1;
  flex-grow: 1;
  max-width: 350px;
  min-width: 250px;
}
.fed-label{
  line-height: 3.3rem;
  margin-right: 10px;
}
hr.bl-divider {
  border: 1px solid lightgray;
}

hr.bl {
  border-top: 2px solid #154285;
  border-left: none;
  border-right: none;
  border-bottom: none;
}

hr.bl-bottom {
  border-top: 2px solid #154285;
  border-left: none;
  border-right: none;
  border-bottom: none;
  margin-top: 3em;
}


#content .az-list li {
    display: block;
    width: 40px;
    height: 40px;
    color: white;
    text-align: center;
    line-height: 40px;
    font-size: 110%;
    padding: 0 0 0 0;
}
#content .az-list li.current, .az-list li a:hover {
    background: white;
    border: 2px solid $AZ-button;
    color:$AZ-button;
    font-weight: bold;
}
#content .az-list li.current>div {
  margin-top: -120px;
  padding-top: 120px;
}
.az-list li a {
    color: white;
    background: $AZ-button;
    display: block;
}
.azNextPrev{
  display: flex;
  justify-content: space-between;
  font-size: 2.5em;
  font-weight: bold;
}
.nextLetter, .prevLetter{
  background-repeat: no-repeat;
  background-position-y: center;
  background-size: 40px;
  text-decoration: none;
  &:hover{
    text-decoration: none;
  }
}
.nextLetter{
  background-image: url(../images/arrow-right.svg);
  background-position-x: 2.5rem;
  padding-right: 2.5rem;
  margin-bottom: 3rem;
}
.prevLetter{
  background-image: url(../images/arrow-left.svg);
  background-position-x: -1.4rem;
  padding-left: 2.5rem;
  padding-bottom: 0px;
  margin-bottom: 3rem;
}

.one-column-bullet li{
   margin: 0 0 10px 0;
 }

 .custom-select{
   width: 295px;
   overflow: hidden;
   border: 1px solid $AZ-customselect-border;
   background: url("../images/Icon_Arrow_Dropdown.png") 95% 50% no-repeat;
   background-color: #fff;
   display: inline-block;
   vertical-align: text-top;
   margin: 0px 5px 0px 0px;
 }
 .custom-select select{
   width: 293px;
   -webkit-appearance:none;
   -moz-appearance:none;
   -o-appearance:none;
    appearance:none;

   font-size: 1em;
   height: 36px;
   padding: 0 0 0 10px;
   border: none;
   border-radius: 0;
   background-color: transparent;
 }
 .custom-select select::-ms-expand{
   display:none;
 }

 @media (min-width: 600px) {
 .custom-select {
     margin: 0px 20px 70px 0px;   
   }
 }
 @media (min-width: 481px) and (max-width: 599px) {
 .custom-select {
   margin: 0px 20px 20px 0px;   
 }
 }
