/***********************
1. General & Misc
***********************/
body {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  /*font-family:  Tahoma, Verdana, Arial, Sans-serif;*/
  font-size: 90%;
  background-color: #fff;
  color: #000;
  -webkit-text-size-adjust: 100%;
}
main{
  display: block;
}

::-moz-selection { /* Code for Firefox */
    color: #fff;
    background: $text-selection;
}

::selection {
    color: #fff;
    background: $text-selection;
}
.group:after {
  content: "";
  display: table;
  clear: both;
}

a{
  color:$blue;
  text-decoration: underline;
}
a:visited{
  color:$visited-link;
}
a:hover, a:focus, a:active{
  color:$link-hover;
}
a:focus, input:focus, button:focus, textarea:focus, select:focus, a:active, input:active, button:active, textarea:active, select:active{
  outline:$focus solid 3px;
}
h1{
  margin: 0 0 0 0;
}

h2 {
  font-size: 140%;
  font-weight: normal;
  padding: 0 0 0 0;
}

ul li, ol li{
  line-height: 20px;
  padding:3px 0 9px 0;
}

li>p {
	margin: 0 0 0 0;
	display: inline-table;
}

#features-landing li>p {
	margin: 0 0 0 0;
	display: inline;
}

input {
    -webkit-appearance: none;
    border-radius: 0;
}

input[type="checkbox"]{-webkit-appearance: checkbox; 
}
input[type="radio"]{-webkit-appearance: radio; 
}

#alltpcs-link { 
	width:100%; 
	text-align:center; 
	display: inline-block;
	margin-top: -65px;
} 

#alltpcs-link p {
  margin-bottom: 0;
}

#alltpcs-link a {
  color:#fff;
  text-decoration: none;
}

#alltpcs-link img { 
	position:static; 
	top:50%;
  left:50%;
  width:45px;
}

#tpcs {
  padding-top: 110px;
  margin-top: -90px;
  color: $red;
  font-size: 160%;
  font-weight: 700;
  }
 
@media (max-width: 992px) {

  body{
    font-size:100%;
  }
  h2{
    font-size: 117%;
  }
  
}

@media (max-width: 991px) {
  
  #content ul li, #content ol li{
    line-height: 25px;
  }
}

@media (max-width: 950px) {
  #tpcs {
    padding-top: 80px;
    margin-top: -65px;
    color: $red;
    font-size: 160%;
    font-weight: 700;
  }
}