/***********************
2. Left Navigation
***********************/
.leftnav{
 background: #fff;
 padding: 17px 20px 0 0;
 margin: 0 0 50px 0;
}

.shade{
 background: $beige;
 padding: 1px 0 5px 0;
}

.shade ul{
 padding: 5px 0 0 0;
}

.shade ul li{
 list-style-type: none;
 padding: 0 0 0 0;
 background: $beige;
}

.shade ul li a{
 padding: 5px 10px 5px 15px;
 line-height: 18px;
 margin: 0 0 0 6px;
}

.shade ul li.slctd{
 background: $red;
}

.shade ul li.slctd a{
 background: #fff;
 font-weight: normal;
 color: $red;
}
.shade ul li.slctd a:hover{
 text-decoration: none;
}

.shade ul li a{
 text-decoration: none;
 display: block;
}

.shade ul li a:hover{
 text-decoration: underline;
}

.shade header h2{
 background: #fff;
 font-size: 100%;
 margin: 3px auto 0 auto;
 padding: 10px 10px 10px 0px;
 /*color: $dark-gray;*/
 font-weight: bold;
}

.shade header h2 a{
   color: $blue;
   text-decoration: none;
}

.shade header h2 a:hover{
   color: $blue;
   text-decoration: underline;
}

.mrtp{
   display: none;
}

.mrtp button {
   width: 100%;
   text-align: left;
   font-size: 90%;
   border: none;
     padding: 1px 15px 1px 15px;
   margin: 5px 0 0 0;
   display: flex;
     justify-content: space-between;
   background: $floating-box;
 }

 .mrtp button h2{
   color: #fff;
   margin: 0 0 0 0;
 }

 .shade header span:before {
   content: "< ";
   font-weight: normal;

}

 #content div.shade ul {
   margin: 0 0 10px 0;
}

 #content div.shade ul li {
   padding: 0 0 0 0;
}

 .arrowLft {
     display: block;
     padding-left: 20px;
     text-indent: -19px;
}

@media (min-width: 992px) {
	.shade {
    display: block!important;
	}
}

@media (max-width: 991px) {

  .leftnav {
		padding: 0 0 0 0;
    margin: 0 0 0 0;
	}
}