
@mixin default-box {
  position: absolute;
  right: 0;
  top: -40px;
  height: 40px;
  width: auto;
  margin: 0;
  background-color: white;
  border-radius: 0;
  color: $blue;
  padding-right: 40px;
  background-image: url('../images/close-red.svg');
  background-position: 90% 50%;
  background-repeat: no-repeat;
  background-size: 20px;
  font-size: 1.7rem;
  &:hover{
    background-color: $modal-top-close-hover;
  }
}

.modal{
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9000;
  text-align: center;
  overflow: auto;
  &.active{
    display: block;
  }
  >div>div{
    background: white;
    position: relative;
    top: 80px;
  }
  h2{
    color: $modal-heading;
  }
  p{
    max-width: 600px;
    margin: 20px auto;
    padding: 0 40px;
  }
  .close{
    width: auto;
    background-color: $aqua-blue;
    color: $button-text;
    margin: 0 0 20px;
    &:hover {
      background-color: $aqua-darker;
    }
    &.top{
      @include default-box;
    }
  }
  .icon{
    height: 100px;
    background-color: $modal-heading;
    background-image: url('../images/BUSA_lightbox_icon.png');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: auto 70px;
  }
  .logo{
    height: 100px;
    margin-bottom: 50px;
    background-color: $modal-icon-bg;
    background-image: url('https://app-usa-modeast-test-a01239f.s3.amazonaws.com/Logo_USAGov.png');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: auto 70px;
    @media (max-width: 480px) {
      background-size: auto 65px;
    }
    &:lang(es) {
      background-image: url('https://app-usa-modeast-test-a01239f.s3.amazonaws.com/Logo_USAGov_Spanish.png');
      @media (max-width: 340px) {
        background-size: auto 65px;
      }
    }
  }
}
#emergency-modal{
  .icon{
    background-color: $modal-emergency-icon-bg;
    background-image: url('../images/warning-white.svg');
  }
}
html.modal-open{
  overflow-y: hidden;
}
#vocModal .vocCancelButton.top-right{ //The .vocCancelButton class is added here to override styles from the voc tool
  @include default-box;
}
