@media screen and (max-width: 407px){
	.tooltip-left .tooltip-left-text {
		top: 120%;
		margin-left: -60px;
	}
}

@media screen and (max-width: 390px) {
	.tooltip-left .tooltip-left-text {
		top: 115%;
		margin-left: -75px;
	}
}

@media screen and (max-width: 320px) {
	.tooltip-left .tooltip-left-text {
		width: 140px;
		top: 120%;
		margin-left: -45px;
	}
}

