
$headerHeight:162px; //Height of header minus gov banner
$headerHeightStuck: 101px; //Height of header minus gov banner when stuck
$headerHeightMobile:137px; //Height of mobile header
$headerHeightDiff: $headerHeight - $headerHeightStuck; //162-101=61 The change in header height (stuck vs unstuck)

#backToTop{
  position: fixed;
  height: 50px;
  width: 90px;
  right: -100px;
  bottom: 80px;
  background: #e8f0f7;
  transition: right 0.5s;
  background-image: url(../images/Icon_Back_to_Top_Blue.png);
  background-size: 45px;
  background-position: 2px;
  background-repeat: no-repeat;
  padding: 0 0 0 45px;
  border-radius: 25px 0 0 25px;
  line-height: 50px;
  display: inline-block;
  text-align: center;
  
  &.stuck{
    right: 0;
  }
}

header h2[id], #content{//This offsets linkable headings so they appear below sticky-nav when their link is used
	padding-top: calc( #{$headerHeightMobile} + 0.5rem );
	margin-top: -$headerHeightMobile;
	@media screen and (min-width: 951px){
		padding-top: calc( #{$headerHeightStuck} + 0.5rem );
		margin-top: -$headerHeightStuck;
	}
}

.usa-skipnav{
	z-index: 7001;
}
.skip{//This offsets skipnav target so it appears below sticky-nav when their link is used
	display: block;
	padding-top: calc( #{$headerHeightMobile} + 0.5rem );
	margin-top: -$headerHeightMobile;
	&:focus{
		outline: none;
	}
}

.leftnav{
  position: -webkit-sticky;
  position: sticky;
  top: 84px;
  z-index: 1;
}

 @media (max-width: 991px) {
  .leftnav {
    position: relative;
    top: unset;
	}
}


@media screen and (min-width: 951px){
  .usa-logo img {
    transition: width 0.2s;
  }
  .stuck .usa-logo img {
    width: 40px;
  }
  .stuck  .cntrlogo:lang(es) img, .stuck  .usa-logo:lang(es) img {

    width: 100px;
  }

  .usa-header {
    transition: margin-bottom 0.2s;
  }
  .usa-header.stuck {
    margin-bottom: $headerHeightDiff; //This prevents jitter caused by scroll events firing during the header height transition. It should match the amount that the header height changes.
  }

  .usa-header-extended .usa-nav-link {
    transition: padding-top 0.2s;
  }
  .usa-header-extended.stuck .usa-nav-link {
    padding-top: 0.7rem;
  }
  .usa-nav-link span{
    transition: height 0.2s;
  }
  .usa-header.stuck .usa-nav-link span{
    height: 38px;
    padding-bottom: 0;
    border-bottom-width: 0.5rem;
  }
  .usa-header.stuck .usa-nav-primary button[aria-expanded=true] span{
    border-bottom-width: 0.5rem;
  }

  .skip{
		padding-top: calc( #{$headerHeightStuck} + 0.5rem );
    margin-top: -$headerHeightStuck;
  }
}