
/***********************
8. Feature Page
***********************/
.bylinebld {
	font-weight: bold;
	text-transform: uppercase;
}
.bld {
	font-weight: bold;
}
#pipe {
	border-style: solid;
	border-width: 1px 0;
	border-color: $feature-pipe;
	padding: 5px 0 5px 0;
	margin: 20px 0 15px 0;
}
#content .pipe {
	padding: 5px 0 5px 0;
}
.line {
	padding: 3px 0px 3px 0px;
}
.rel-txt {
	overflow: hidden;
}
span.scayt-misspell-word {
	display: inline-block;
}
a>span.scayt-misspell-word {
	display: inline;
}
.feature-img {
	width: 100%;
	max-width: 795px;
	margin: auto;
	padding: 10px 20px 10px 20px;
}
#featurebox {
	padding: 0 0 0 10px;
	margin: 25px 0 0 0;
	border: 1px solid $featurebox-border;
	text-decoration: none;
	width: 100%;
}
#featurebox ul {
	padding: 0 0 0 0;
	margin: 0 0 0 0;
	list-style: none;
}
#featurebox ul li {
	padding: 10px 0 0 0;
	list-style: none;
	overflow: auto;
}
#featurebox header h3 {
	color: $blue;
	outline: 0px !important;
	border: 0px !important;
	font-size: 130%;
	font-weight: bold;
	margin: 0 0 0 0;
}
#featurebox a {
	border: none;
}
#featurebox h2 {
	font-size: 140%;
	color: $red;
	padding: 0px 0px 0px 0px;
	font-weight: normal;
}
#featurebox p {
	overflow: hidden;
	color: #000;
	padding: 0 5px 0 0;
	display: initial;
}

#featurelist {
	border: none;
	background-color: $featurelist;
}
.rel-img, .feabox-img, .whats-img {
	width: 100%;
	max-width: 170px;
	padding: 0px 18px 10px 0px;
	float: left;
}

#featurelist ul {
	list-style-type: none;
	padding: 0 0 0 0;
}
#featurelist ul li {
	padding: 10px 18px 10px 18px;
	border-bottom: 1px dotted $downlevel-h2;
	overflow: hidden;
}
#featurelist-video {
	border: none;
	background-color: #FFF;
}

#featurelist-video ul {
	border-bottom: 1px dotted $downlevel-h2;
	overflow: hidden;
}
#featurelist ul>li:last-child {
	border-bottom: none;
}

#featurelist h2 {
	font-size: 140%;
	display: block;
	color: #fff;
	padding: 12px 0 15px 65px;
	margin: 0 0 0 0;
	/*max-width: 855px;*/
	height: auto;
	background: $AZ-button url('../images/Related_Content_Icon.png') left 20px top 12px no-repeat;
}

#features-landing header h2 {
	color: $blue;
	outline: 0px !important;
	border: 0px !important;
	font-size: 130%;
	font-weight: bold;
	margin-bottom: 15px;
}
#features-landing header h2 a {
	color: $blue;
	text-decoration: none;
}
#features-landing header h2 a:hover {
	text-decoration: underline;
}
#features-landing header h2 a:visited {
	color: $visited-link;
}
#featurelist header h3 {
	color: $blue;
	outline: 0px !important;
	border: 0px !important;
	font-size: 140%;
	font-weight: bold;
	margin: 0px;
}
#featurelist-video header h3 {
	color: $blue;
	outline: 0px !important;
	border: 0px !important;
	font-size: 140%;
	font-weight: bold;
}
#featurelist header h3 a, #featurelist-video header a, #featurebox header h3 a, #featurebox-video header h3 a {
	color: $blue;
	text-decoration: none;
}
#featurelist header h3 a:hover, #featurebox header h3 a:hover, #featurelist-video header h3 a:hover, #featurebox-video header h3 a:hover {
	text-decoration: underline;
}
 #featurelist header h3 a:visited, #featurelist-video header h3 a:visited, #featuresbox header h3 a:visited, #featurelist-video header h3 a:visited, {
 color: $visited-link;
}
#featurelist li p, #featurelist-video li p {
	margin: 10px 0 0 0;
	display: initial;
}
.featuremore {
	display: block;
	border: 1px solid $featurebox-border;
	padding: 15px 15px 15px 0px;
	margin: 0 15px 15px 0;
	width: 100%;
	font-weight: bold;
	text-decoration: none;
	text-align: right;
}
a:hover.featuremore {
	border: 1px solid $blue;
	text-decoration: underline;
}
.featuremore span {
	display: inline-block;
	background-image: url("../images/Icon_Arrow_More_Service.png");
	width: 33px;
	height: 33px;
	margin: 0 0 0 10px;
	vertical-align: middle;
	background-size: contain;
}
#features-landing {
	padding: 0;
}
#content ul#features-landing>li {
	list-style: none;
	padding: 15px 0 10px 0;
	border-top: 1px dotted $downlevel-h2;
	height: auto;
	display: inline-block;
	width: 100%;
}
#content ul#features-landing>li:first-child {
	border: none;
}
#features-landing h3, #features-landing p {
	margin: 0;
}
#content ul.pagination li {
	display: block;
	width: 40px;
	height: 40px;
	float: left;
	margin: 5px 10px 10px 10px;
	text-align: center;
	line-height: 40px;
	font-size: 100%;
	padding: 0 0 0 0;
}
.element-invisible {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px);
	clip: rect(1px, 1px, 1px, 1px);
	overflow: hidden;
	height: 1px;
}
.pager-ellipsis {
	margin: 15px -15px 0 -15px
}
#content ul.pagination li.previous, #content ul.pagination li.next {
	width: auto;
	margin: 5px 10px 5px 10px;
	display: inline-block;
	line-height: 39px;
}
#content ul.pagination li.previous a {
  	color:$nav-bg;
 	padding: 0 10px 0 20px;
 	border: 2px solid $aqua-blue;
 	background: $aqua-blue;
 	font-weight: bold;
 	background-image: url("../images/arrow-left-darkgray.svg");
 	background-size: 7rem;
 	background-repeat: no-repeat;
 	background-position: -25px -25px;
 	width: auto;
  }

#content ul.pagination li.previous a:hover {
  	color: $nav-bg;
 	background: $aqua-darker;
 	font-weight: bold;
 	padding: 0 10px 0 20px;
 	border: 2px solid $aqua-darker;
 	background-image: url("../images/arrow-left-darkgray.svg");
 	background-size: 7rem;
 	background-repeat: no-repeat;
 	background-position: -25px -25px;
 	width: auto;
  }

 #content ul.pagination li.next a {
  color:$nav-bg;
 	padding: 0 20px 0 10px;
 	border: 2px solid $aqua-blue;
 	background: $aqua-blue;
 	font-weight: bold;
 	background-image: url("../images/arrow-right-darkgray.svg");
 	background-size: 7rem;
 	background-repeat: no-repeat;
 	background-position: right -25px bottom 9px;
 	width: auto;
  }

#content ul.pagination li.next a:hover {
  color: $nav-bg;
 	background: $aqua-darker;
 	font-weight: bold;
 	padding: 0 20px 0 10px;
 	border: 2px solid $aqua-darker;
 	background-image: url("../images/arrow-right-darkgray.svg");
 	background-size: 7rem;
 	background-repeat: no-repeat;
 	background-position: right -25px bottom 9px;
 	width: auto;
  }

.pagination li.current, .pagination li a:hover {
	background: white;
	border: 2px solid $AZ-button;
	color: $AZ-button;
	font-weight: bold;
	padding: 0px 5px;
}
.pagination li a {
	color: white;
	background: $AZ-button;
	display: block;
	width: 100%;
	height: 100%;
	text-decoration: none;
}
#content ul.pagination {
	padding: 0 0 0 0;
	position: relative;
	display: flex;
	justify-content: center;
	margin: 0 0 0 0;
}
#content ul.pagination li {
	display: block;
	width: 40px;
	height: 40px;
	float: left;
	margin: 5px 5px 5px 5px;
	text-align: center;
	line-height: 40px;
	font-size: 100%;
	padding: 0 0 0 0;
}
