#content ul.pagination li.pager-ellipsis {
	width: 20px;
	line-height: 60px;
}
#content ul.pagination li.pager-dscrpt, #content ul.pagination li.pager-dscrpt2 {
	display: none;
}

@media (max-width: 991px) {
#featurelist header h3, #features-landing header h2, #featurebox h2, #featurebox header h3 {
	font-size: 120%;
}
#featurelist h2 {
	font-size: 120%;
}
#featurelist ul {
	list-style-type: none;
	padding: 0 0 10px 0;
}
#featurelist ul li {
	padding: 10px 18px 0px 18px;
}
#featurelist {
	border: none;
	background-color: $featurelist;
}
#featurebox ul li {
	padding: 10px 0 0 0;
	list-style: none;
	overflow: auto;
}
.rel-img, .feabox-img, .whats-img {
	width: 100%;
	padding: 0px 18px 10px 0px;
}
}

@media (max-width: 768px) {
.rel-img, .feabox-img, .whats-img {
	width: 100%;
	padding: 0px 18px 10px 0px;
}
/* .feature-img{
  	width: 100%;
  } */

}

@media all and (max-width: 600px) {
#content ul.pagination li.pager-ellipsis, #content ul.pagination li.current, #content ul.pagination li.pager-item {
	display: none;
}
#content ul.pagination li.pager-dscrpt, #content ul.pagination li.pager-dscrpt2 {
	display: inline;
	width: auto;
	font-weight: normal;
}
}

@media (max-width: 480px) {
#featurelist h2 {
	/* font-size: 120%; */
	display: block;
	color: #fff;
	padding: 12px 0 15px 65px;
	margin: 0 auto 0 auto;
	max-width: 855px;
	height: auto;
	background: $AZ-button url('../images/Related_Content_Icon.png') left 20px top 12px no-repeat;
}
#featurelist ul {
	list-style-type: none;
	padding: 0 0 10px 0;
}
#featurelist ul li {
	padding: 10px 18px 0 18px;
}
#featurelist {
	border: none;
	background-color: $featurelist;
}
.rel-img {
	width: 100%;
	max-width: none;
	margin: auto;
	padding: 0px 20px 20px 20px;
}

#featurebox .rel-img {
	padding: 0px 20px 20px 10px;
}

.whats-img {
	width: 100%;
	max-width: none;
	margin: auto;
	padding: 0px 20px 20px 10px;
}
.feabox-img {
	width: 100%;
	max-width: none;
	margin: auto;
	padding: auto;
}
/*#content ul.pagination li.previous a,  #content ul.pagination li.next a {
	padding: 0 6px 0 6px;
}
#content ul.pagination li.previous a:hover,  #content ul.pagination li.next a:hover {
	padding: 0 6px 0 6px;
}*/
.feature-img {
	width: 100%;
}
}

@media (min-width: 401px) {
#content ul.pagination li.pager-dscrpt, #content ul.pagination li.pager-dscrpt2 {
	line-height: 40px;
}
}
@media (max-width: 400px) {

#content ul.pagination li.previous, #content ul.pagination li.next {
	width: auto;
	margin: 5px 10px 5px 10px;
	display: inline-block;
	line-height: 39px;
}

#content ul.pagination li.pager-dscrpt2:first-child{
	line-height: 40px;
}

#content ul.pagination li.pager-dscrpt2{
	line-height: 20px;
}
}
@media (max-width: 320px) {
#content ul.pagination li.pager-dscrpt:first-child{
	line-height: 40px;
}
#content ul.pagination li.pager-dscrpt {
	line-height: 20px;
}
}
