/***********************
11. Contact Center Box
***********************/
#cntctbx {
    border: 1px solid $contact-center-box-border;
    margin: 0 0 25px 0;
}

#cntctbx h2 {
    font-size: 130%;
    display: block;
    color: $contact-center-box-heading;
    padding: 14px 0 14px 65px;
    margin: 0 auto 0 auto;
    height: auto;
    border-bottom: 1px solid $contact-center-box-border;
    background: $contact-center-box url('../images/Contact_Center_Icon.svg') left 10px top 3px no-repeat;    
}

#cntctbx.no-icon h2 {
    color: $blue;
    padding: 14px 0 14px 25px;
    background: $contact-center-box;
}

#cntctbx ul {
    margin: 0 0 15px 15px;
}

#cntctbx p {
    padding: 0 10px 0 25px;
}

#cntctbx .usa-search {
    padding: 0 10px 0 25px;
    margin: 0 0 25px 0;
}

@media (max-width: 991px) {

  #cntctbx h2 {
      font-size: 120%;
  }
}

@media (max-width: 379px) {
    #cntctbx h2 {
        font-size: 100%;
        background: #d9d9d9 url(../images/Contact_Center_Icon.svg) left 10px top 0px no-repeat;
        background-size: 50px;
}
}

@media (max-width: 320px) {
    #cntctbx h2 {
        font-size: 90%;
        padding: 14px 0 14px 55px;
        background: #d9d9d9 url(../images/Contact_Center_Icon.svg) left 12px top 0px no-repeat;
        background-size: 45px;
}
}