/***********************
3. Header
***********************/

//TODO: See if it is safe to remove most of the cntrlogo and usa-logo styles

.usa-header{
  position: -webkit-sticky;
  position: sticky;
  top: -22px;
  background: #fff;
  @media (max-width: 480px) {
    top: -42px;
  }
}
.usa-mobile_nav-active .usa-header {
  position: unset; //Needed for mobile nav to work with sticky-nav
}

.cntrlogo, .usa-logo {
  margin-left: auto;
  margin-right: auto;
}

.cntrlogo:lang(es), .usa-logo:lang(es) {
  width: 485px; /*sg-4/20/16 - changed from 560px*/
  height: 157px;
  padding: 65px 0 0 0;
}

.cntrlogo a, .cntrlogo a:visited, .usa-logo a, .usa-logo a:visited{
  //font-size: 60px;
  color: #fff;
  display: block;
}

#logo{
  border: 0;
  width: 100%;
}

.usa-nav-primary .topic-link {
  float: left;
  width: 100%;
  padding-left: 70px;
  background-position: 10px 3px;
  background-repeat: no-repeat;
  background-size: 55px;
  &.icon-plus{
    background-position: left 10px center;
  }
  .usa-button {
    display: inline-block;
    width: auto;
    background-color: $aqua-blue;
    color: $button-text;
    padding-right: 30px;
    background-image: url(../images/arrow-right-darkgray.svg);
    background-size: 7rem;
    background-repeat: no-repeat;
    background-position: center right -20px;
    text-align: left;

    &:hover, &:active, &:focus {
      color: $button-text;
      background-color: $aqua-darker;
      text-decoration: underline;
    }
    &:active, &:focus {
      box-shadow: none;
      outline: $focus solid 3px;
    }
  }
  @media (max-width: 950px) {
    float: none;
    padding-left: 45px;
    background-size: 45px;
    background-position: 0px center;
    &.icon-plus{
      background-position: 0px center;
    }
  }
}

/** style to override standards **/
.usa-nav ul li:before {
  content: "";
  display: table-cell;
  padding-right: 0.4em;
}

.usa-navbar.breadcrumb {
  height: 5rem;
  text-align: initial;
}
.usa-navbar.breadcrumb a {
  line-height: 5rem;
  padding-left: 3.8rem;
  background-image: url(../images/arrow-left.svg);
  background-repeat: no-repeat;
  background-position-x: 2px;
  background-position-y: center;
  background-size: 40px;
}
@media screen and (min-width: 951px){
  .usa-header-extended .usa-navbar.breadcrumb {
    display:none;
  }
}

@media screen and (min-width: 951px){
  .usa-nav-primary > li.mobile-only{
    display: none;
  }
}

@media (max-width: 992px) {
   #logo {
    width: auto;
    }
}

@media (max-width: 767px) {
  #logo {
    height: 50px; /*changed from 75px to 50px - sg 3/15/16 */
  }
}

@media (max-width: 480px) {
  #logo:lang(es) {
    height: 34px;
  }
}
@media (max-width: 350px) {
  #logo:lang(es) {
    height: 27px;
  }
}

@media (max-width: 340px){
  .usa-navbar.breadcrumb a {
    line-height: 2.3rem;
    display: inline-block;
  }
}
