14. Tagline
***********************/

.footertagline{
   background: #000;
   clear:both;
 }

.footerlogo
{
background: $light-gray;
clear: both;
}

.tagline, .footerlogoLine{
 margin: 0 auto 0 auto;
 padding: 2rem 0 1rem 5px;
}

.tagline p, .footerlogoLine p{
 float: left;
 color: #fff;
}

.tagline .volver{
 float:right;
 padding:  0 14px 0 0;
}

.footerlogoLine .volver{
 float:right;
 padding: 0 14px 0 0;
}