/***********************
15. Return to top buttons
***********************/

//TODO: use a single name for 'Back to Top'

.statebacktotop{
 padding: 160px 0 5px 0;
 text-align:right;
 }

.volver, .toTop{
  padding:10px 0 5px 0;
  text-align:right;
	margin: 0;
}
.volver a, .toTop a{
  display:inline-block;
  padding:0 0 4px 0;
  text-decoration:none;
}
.volver a:hover, .volver a:focus, .volver a:active,
.toTop a:hover, .toTop a:focus, .toTop a:active{
  text-decoration:underline;
}

.footer .volver{
  padding:15px 0 15px 0;
  text-align:center;
}

@media (max-width: 991px) {

	.volver, .toTop{
		text-align:center;
	}
	.volver a{
  	  padding:0 0 0 0;
  	}

  	.statebacktotop{
  	text-align: center;
  	padding: 60px 0 0 0;
   }
   
   .volverspcing {
    padding: 10px 0 10px 0;
    margin: 0 0 0 0;
    }
    .footerlogoLine .volver{
		float:right;
		padding: 0px;
}
}

@media (max-width: 480px) {
    
  .footerlogoLine .volver{
		float:right;
		padding: 0px;
  }
}