/***********************
9. Social Media
***********************/
.sclmedia{
 width: 100%;
 background-color: $beige;
}
.sclmedia .container {
 max-width: 900px;
}
.cntr{
 width: 70%;
 margin: 0 auto 0 auto;
 padding: 20px 0 0 0;
}

.scndcntr{
 float: left;
}

.dvdr {
 border-left: 2px dotted $social-media-divider;
 margin: 20px 0 20px 0;
 padding: 0 15px 10px 38px;
}

.nodvdr {
   margin: 20px 0 20px 0;
}

.noeml {
 display: none;
}

.noemllft {
   margin-left: 30%;
   text-align: center;
}

.sclmedia h2{
 margin: 0 0 10px 0;
 color: $social-media-heading;
 font-weight: normal;
}

 .sclmedia ul {
   min-height: 78px;
 margin: 0 0 40px 0;
 padding: 0 0 0 0;
 width: 100%;
}

.sclmedia ul li{
 margin: 0 0 0 0;
 padding: 0 0 0 0;
 list-style-type: none;
}

.sclmedia ul li a{
 margin: 3px 0 0 0;
}

 .sclfcbk {
   background: url('../images/Icon_Connect_Facebook.png') 28px 5px;
   background-repeat: no-repeat;
 background-size: 22px;
 background-color: $Facebook;
 }

 a.sclfcbk{
   display: block;
 width: 78px;
 padding: 33px 0 0 0;
 text-decoration: none;
 }

  a.sclfcbk span{
   display: block;
   width: 78px;
   padding: 7px 0 7px 0;
 text-align: center;
 background-color: #fff;
 }

 a:hover.sclfcbk span {
 background-color: $Facebook-hover;
 color: #fff;
 }

  .scltwttr {
   background: url('../images/Icon_Connect_Twitter.png') 28px 5px;
   background-repeat: no-repeat;
 background-size: 25px;
 background-color: $Twitter;
 }

 a.scltwttr{
   display: block;
 width: 78px;
 padding: 33px 0 0 0;
 text-decoration: none;
 }

  a.scltwttr span{
   display: block;
   width: 78px;
   padding: 7px 0 7px 0;
 text-align: center;
 background-color: #fff;
 }

 a:hover.scltwttr span {
 background-color: $Twitter-hover;
 color: #fff;
 }

 .sclggle {
   background: url('../images/Icon_Connect_Google.png') 28px 5px;
   background-repeat: no-repeat;
 background-size: 24px;
 background-color: $GooglePlus;
 }

 a.sclggle{
   display: block;
 width: 78px;
 padding: 33px 0 0 0;
 text-decoration: none;
 }

  a.sclggle span{
   display: block;
   width: 78px;
   padding: 7px 0 7px 0;
 text-align: center;
 background-color: #fff;
 }

 a:hover.sclggle span {
 background-color: $GooglePlus-hover;
 color: #fff;
 }

 .scleml {
   background: url('../images/Icon_Email_1.png') 28px 5px;
   background-repeat: no-repeat;
 background-size: 23px;
 background-color: $Email;
 }

 a.scleml{
   display: block;
 width: 78px;
 padding: 33px 0 0 0;
 text-decoration: none;
 }

  a.scleml span{
   display: block;
   width: 78px;
   padding: 7px 0 7px 0;
 text-align: center;
 background-color: #fff;
 }

 a:hover.scleml span {
 background-color: $Email-hover;
 color: #fff;
 }

input#email-input{
 height: 38px;
 width: 275px;
 border: 1px solid $AZ-customselect-border;
 padding: 0 0px 0 10px;
}

.sclmedianoml{
 margin: 0 30% 0 30%;
}

.nowrp{
 white-space: normal;
}

#emailSubmit {
 display: inline-block;
 text-align: center;
 float: right;
 height: 38px;
 margin: 0 0 0 0;
 color: $button-text;
 font-size: 100%;
 font-family:  Tahoma, Verdana, Arial, Sans-serif;
 line-height: 18px;
 padding: 6px 12px 6px 12px;
 border-top: 1px $aqua-blue solid;
 border-right: none;
 border-bottom: 1px $aqua-blue solid;
 border-left: none;
 position: relative;
 font-weight: bold;
 background-color: $aqua-blue;
 /*vertical-align: text-top;*/
}

#statego {
  display: inline-block;
  text-align: center;
  float: right;
  height: 38px;
  margin: 3px 0 0 0;
  color: $button-text;
  font-size: 100%;
  font-family: Tahoma, Verdana, Arial, Sans-serif;
  line-height: 18px;
  padding: 6px 12px 6px 12px;
  border: 1px $aqua-blue solid;
  position: relative;
  font-weight: bold;
  background-color: $aqua-blue;
  vertical-align: text-top;
  width: auto;
  }

#statego:hover{
  background-color:#00a6b2;
  text-decoration:underline;
}

#statego{float: none;}

@media (min-width: 992px) {

 	.noemllft {
    margin-left: 26%;
    text-align: center;
 	}
}

@media (max-width: 991px) {

	.sclmedia h2{
		margin: 20px 0 10px 0;
		text-align: center;
    font-size: 120%;
 	}
	a.sclfcbk span, a.scltwttr span, a.sclggle span, a.scleml span{
		font-size: 95%;
	}

	.dvdr {
		border-left: none;
		margin: 0 0 0 0;
		padding: 0 0 0 0;
 	}

 	.nodvdr {
		margin: 0 0 0 0;
 	}

 	.noemllft {
        margin-left: 0;
        margin-bottom: 20px;
 	}

 	.sclmedia ul{
		margin: auto auto auto auto;
		max-width: 260px;
 	}
	.sclmedia ul li a{
	margin: 3px auto 0 auto;
 	}
	 input#emailSubmit{
		float: none;
 	}

 	#emailSubmit {
		margin: 10px 0 25px 0;
	}

	p.txtbx{ //TODO: Is this ever used? Does it belong here?
		text-align: center;
		width: 100%;
	}
	.sclmedianoml{
 		margin: 0 auto 0 auto;
 	}
}

@media (max-width: 768px) {

	.sclmedia ul li{
		margin: 0 0 0 0;
		padding: 0 0 25px 0;
		list-style-type: none;
 	}

	.nowrp{
   	white-space: normal;
  	padding: 0 0 0 0;
 	}
  .cntr input.text{
	  width: 100%;
 	}
}

@media screen and (max-width: 480px) {
 #statego {
   width: 295px;
   margin-top: 40px;
 }
}
