/***********************
6. downlevel-directory
***********************/
section.otln > ul li:only-child {
 list-style-type: none;
 margin: 0 0 0 -19px;
}

.otln{
 border: 2px solid $table-stripe;
 min-height:50px;
 margin: 0 0 0 0;
 padding: 10px 0 10px 0;
}

.otln h3, .otln h4{
 float: left;
 font-size: 90%;
 width: 30%;
 margin: 0 0 0 0;
 padding: 0 0 10px 25px;
 color: #000;
}

.otln ul{
 margin: 0 0 0 30%;
}

.otln ul li{
 line-height: 25px;
 padding: 0 25px 0 0;
}

.otln p{
 margin: 0 0 0 33%;
 padding: 0 0 0 0;
}

.otln p a{
 word-wrap: break-word;
}

.otlnhdr{
 background-color: $AZ-button;
 min-height:40px;
 margin: 0 0 0 0;
 padding: 10px 0 10px 0;
}
.otlnhdr h3, .otlnhdr h4{
 color:$white;
 margin: 0 0 0 25px;
 font-size: 100%;
}

.otlnrw{
 border: 2px solid $table-stripe;
 background-color: $table-stripe;
 min-height:50px;
 margin: 0 0 0 0;
 padding: 10px 0 10px 0;
}

.otlnrw header h3, .otlnrw header h4{
 float: left;
 font-size: 90%;
 width: 30%;
 margin: 0 0 0 0;
 padding: 5px 0 10px 25px;
 color: #000;
}

.otlnrw ul{
 margin: 0 0 5px 30%;
}

.otlnrw ul li{
 line-height: 25px;
 padding: 0 25px 0 0;
}

.otlnrw p{
 margin: 0 0 5px 33%;
}

.otlnrw p a {
 word-wrap: break-word;
}

@media (max-width: 991px) {

	.otln ul li{
		padding: 0 25px 12px 0;
	}
 	.otlnrw ul li{
		padding: 0 25px 12px 0
	}
}

@media (max-width: 768px) {

	.otln header h3, .otln header h4{
	float: none;
	width: 100%;
	}

	.otln ul{
		margin: 0 0 0 5px;
	}
	.otln p{
		margin: 0 0 0 25px;
	}

	.otlnrw header h3, .otlnrw header h4{
		float: none;
		width: 100%;
	}

	.otlnrw ul{
		margin: 0 0 0 5px;
	}

	.otlnrw p{
		margin: 0 0 0 25px;
	}
}