/***********************
1. General & Misc
***********************/
#home_logo
{
	float: left;
	width: 413px;
	padding: 0 0 0 15px;
	margin: 0 0 0 0;
}

header h1 {
 	color: $red;
	padding: 10px 0 0 0;
	font-size: 160%;
	font-weight: 700;
	margin-top: 0;
 }

 header h2 {
	color: $blue;
	outline: 0px !important;
	border: 0px !important;
	font-size:  130%;
	font-weight: bold;
 }

 header h3, h3 {
 	color: #000;
	font-size: 110%;
 }
 
 article h3 {
    color: $dark-gray;
    font-size: 110%;
	/*display: inline;*/
 }

.promo-img
{
	float: right;
	padding: 0 15px 0 15px;
}

/***************************
border top and bottom line
***************************/


 #content ul li, #content ol li{
 	line-height: 20px;
 }
.compact-list>li {
  margin: 0;
  padding: 3px 0;
}

 .spk{
 	speak: digits;
 }

 #skiptargetholder {
    margin: 0 0 0 0;
 }
 
 .container { //TODO: This probably belongs somewhere else.
   padding-left: 3rem;
   padding-right: 3rem;
 }
 
 @media (max-width: 991px) {
 	header h1 {
 		font-size: 140%;
  	}
   header h2 {
     font-size: 120%;
   }
 }
 
 @media (max-width: 991px) {

 	.bttn h2 {
 		font-weight: normal;
 	}

	#content ul li, #content ol li{
		padding: 3px 0 9px 0;
		line-height: 20px;
 	}
  
  .one{ //TODO: Is this used? Does it belong here?
		width: 50%;
		margin: 0 auto 0 auto;
	}
  
  .pagenation { //TODO: Is this used? Does it belong here?
  margin: 10px auto 30px auto;
  }
}

@media (max-width: 768px) {
	
	.one{
		width: 80%;
		margin: 0 auto 0 auto;
	}
}

@media (max-width: 480px) {

	.one{
		width: 90%;
		margin: 0 auto 0 auto;
	}
}