/***********************
5. downlevel-topics
***********************/
.infobytpc ul.dwnlvl, .infobytpc ul.topics{
width: 100%;
padding: 0 0 0 15px;
background: #fff;
margin: 0 0 0 0;
}

.infobytpc ul.dwnlvl li, .infobytpc ul.topics li {
 padding-right: 15px;
}

.infobytpc ul.dwnlvl a, .infobytpc ul.topics a{
  display: inline-block;
  text-decoration: none;
  font-size: 110%;
  font-weight: bold;
  margin: 0 0 0 0;
  border: 1px solid #fff;
  border-left: 5px solid #fff;
	padding: 10px 20px 10px 10px;
}

.infobytpc ul li a:hover, .infobytpc ul li a:focus, .infobytpc ul li a:active {
  	border: 1px solid $aqua-blue;
  	border-left: 5px solid $aqua-blue;
	padding: 10px 20px 10px 10px;
 }

.infobytpc ul li p{
line-height: 18px;
margin: 0 0 0 0;
  padding: 2px 0 0 0;
  color: #000;
}

.infobytpc ul.dwnlvl li p, .infobytpc ul.topics li p{
padding: 0 60px 0 0;
line-height: 18px;
padding: 0 0 0 0;
font-weight: normal;
font-size: 1.7rem;
}

.infobytpc ul.dwnlvl li p a, .infobytpc ul.topics li p a{
  padding: 0 0 0 0;
  text-decoration: none;
  margin: 0 0 0 0;
  display: inline-block;
  font-size: 100%;
  font-weight: normal;
  text-decoration: underline;
}

.infobytpc ul li{
list-style-type: none;
margin: 0 0 0 0;
padding: 15px 0 35px 0;
height: 140px;
float: left;
}

.infobytpc ul li a{
text-decoration: none;
margin: 0 0 0 0;
padding: 0 0 26px 65px;
display: block;
}

ul.topics {
  margin: 0 0 0 -40px;
}

.infobytpc ul.topics li a {
	border: 1px solid #fff;
	border-left: 5px solid #fff;
	padding: 10px 0 26px 60px;
}

.infobytpc ul.topics li a:hover, .infobytpc ul.topics li a:focus, .infobytpc ul.topics li a:active {
  	border: 1px solid $aqua-blue;
  	border-left: 5px solid $aqua-blue;
	padding: 10px 0 26px 60px;
 }

