
@charset "UTF-8";

#facetedsearch {
header h1 {
	color: $red;
	padding: 10px 0 0 0;
	font-size: 160%;
	font-weight: 700;
	margin-top: 0;
}
a:visited {
	color: $visited-link;
}
a:visited .icon-backtotop-dwnlvl {
	color: $AZ-button;
}

//TODO: Find out what this is
button.usa-button-primary-alt a:active,
[type="button"].usa-button-primary-alt a:focus,
[type="submit"].usa-button-primary-alt a:focus {
	background-color: $faceted-search-button-active;
	color: $button-text;
}

.introlist {
	margin-bottom: 0
}
.section-box {
	margin-top: 0;
	margin-bottom: 20px;
	background-color: $white;
	padding: 0 1.5rem 1.0rem 1.5rem;
	border: 1px solid $AZ-customselect-border;
	height: auto;
}
.filter-box {
	padding: 0;
	border: 1px solid $light-gray;
	border-radius: 0.3rem;
	height: auto;
}
label {
	max-width: 59rem;
	margin-top: 0;
}
ul li,
ol li {
	line-height: 1.5;
	margin: 5px auto auto auto;  /* bj changed from 15 to 5 */
}
ul.pagination li {
	line-height: 40px;
	margin: 15px 5px 15px 5px;
}
.open-close-filters {
	width: 100%;
	margin: 0 0 0 0;
	padding: 0 0 0 0;
	background: #fff;
}
.open-close-filters > ul {
	margin-bottom: 0;
	display: inline-flex;
	display: -webkit-box-inline-flex;      /* OLD - iOS 6-, Safari 3.1-6 */
	display: -moz-box-inline-;         /* OLD - Firefox 19- (buggy but mostly works) */
	display: -ms-inline-flex;      /* TWEENER - IE 10 */
	display: -webkit-inline-flex;     /* NEW - Chrome */
}
.open-close-filters ul li:before {
	position: absolute;
	content: "";
}
button.usa-accordion-button.usa-banner-button:hover {
	background-color: transparent;
}
.usa-accordion-bordered > ul, .usa-accordion > ul{
	border: none;
}
