}
input, textarea, select {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	// border: 1px solid $nav-close;
	border-radius: 0;
	box-sizing: border-box;
	color: $button-text;
	display: block;
	font-size: 1.7rem;
	line-height: 1.3;
	margin: 0.2em 0;
	max-width: 40rem;
	outline: none;
	// padding: 1rem 0.7em;
	// height: 23px;
}
#daydropdown, #monthdropdown, #yeardropdown {
	width: 220px;
	border: 1px solid $AZ-customselect-border;
	background: url("/sites/all/themes/usa/images/Icon_Arrow_Dropdown.png") 95% 50% no-repeat;
	background-color: #fff;
	vertical-align: text-top;
	margin: 0 50px 0 0;
	display: flex;
}
.posted-select {
	width: 250px;
	overflow: hidden;
	/*border: 1px solid $AZ-customselect-border;*/
	background: url("https://usa.gov/sites/all/themes/usa/images/Icon_Arrow_Dropdown.png") 95% 50% no-repeat;
	background-color: #fff;
	display: inline-block;
	vertical-align: text-top;
	margin: 0 5px 0 0;
}
.posted-select select {
	width: 250px;
	-webkit-appearance: none;
	-moz-appearance: none;
	-o-appearance: none;
	appearance: none;
	font-size: 1em;
	height: 45px;
	padding: 0 0 0 10px;
	/*border: none;*/
	border-radius: 0;
	background: none;
	background-color: transparent;
}
.posted-select select::-ms-expand {
 display: none;
