//TODO: Make these less aggressive
ul.pagination li.previous a {
	color: $button-text;
	background-position: -25px -23px;
	background-sixe: 7rem;
}
ul.pagination li.previous a,
ul.pagination li.previous a:hover,
ul.pagination li.next a,
ul.pagination li.next a:hover {
	display: inline-block;
	padding: 0 15px 0 10px;
	border: none;
}
ul.pagination li.previous a img,
ul.pagination li.previous a:hover img {
	padding-right: 3px;
	margin-bottom: -3px;
}
ul.pagination li.next a img,
ul.pagination li.next a:hover img {
	padding-left: 3px;
	margin-bottom: -1px;
}
ul.pagination li.previous,
ul.pagination li.next {
	border: 2px $aqua-blue solid;
	border-radius: 0.3rem;
	white-space: nowrap;
	height: 41px;
	line-height:31px;
}
ul.pagination li:hover.previous,
ul.pagination li:hover.next {
	border: 2px $aqua-darker solid;
}

ul.pagination button.left:hover {
	height: 40px;
	background: $aqua-darker;
	margin-top: 0.9em;
	color: $button-text;
	padding-left: 1rem;
	padding-right: 1rem;
	white-space: nowrap;
}
ul.pagination button.left span {
	text-align: center;
	padding-left: 2.1px;
	padding-right: 5px;
	padding-left: 15px;
	display: inline-block;
}
ul.pagination button.left img,
ul.pagination button.right img {
	position: absolute;
	top: 32px;
}
ul.pagination button.right {
	height: 40px;
	background: $aqua-blue;
	margin-top: 0.9em;
	color: $button-text;
	padding-left: 1rem;
	padding-right: 1rem;
	white-space: nowrap;
}
ul.pagination button.right:hover {
	height: 40px;
	background: $aqua-darker;
	margin-top: 0.9em;
	color: $button-text;
	padding-left: 1rem;
	padding-right: 1rem;
	white-space: nowrap;
}
ul.pagination button.right span {
	text-align: center;
	display: inline-block;
}
ul.pagination button.right img {
	padding-left: 20px;
}
ul.pagination li.previous a {
	color: $button-text;
	padding: 0 20px 0 12px;
	border: 2px solid $aqua-blue;
	background: $aqua-blue;
	font-weight: bold;
	width: auto;
}
ul.pagination li.previous a:hover {
	color: $button-text;
	background: $aqua-darker;
	font-weight: bold;
	padding: 0 20px 0 12px;
	border: 2px solid $aqua-darker;
	width: auto;
}
ul.pagination li.next a {
	color: $button-text;
	padding: 0 20px 0 10px;
	border: 2px solid $aqua-blue;
	background: $aqua-blue;
	font-weight: bold;
	width: auto;
}
ul.pagination li.next a:hover {
	color: $button-text;
	background: $aqua-darker;
	font-weight: bold;
	padding: 0 20px 0 10px;
	border: 2px solid $aqua-darker;
	width: auto;
}
ul.pagination li.previous,
ul.pagination li.next {
	width: auto;
	margin: 15px 10px 5px 10px;
	display: inline-block;
}
.pagination li.current,
.pagination li a:hover {
	background: white;
	border: 2px solid $AZ-button;
	color: $AZ-button;
	padding: 0px 5px;
}
.pagination li a {
	color: white;
	background: $AZ-button;
	display: block;
	width: 100%;
	height: 100%;
	text-decoration: none;
}
