li.ttli [type="checkbox"] + label,
li.ttli [type="radio"] + label {
	cursor: pointer;
	font-weight: 400;
	margin-bottom: 0.5em;
	margin-left: -34px;
	left: 34px;
	display: inline;
	position: relative;
}
[type="checkbox"] + label,
[type="radio"] + label {
	cursor: pointer;
	font-weight: 400;
	margin-bottom: 0.5em;
	margin-left: -34px;
	/*left: 34px;*/
	display: inline;
	position: relative;
}
[type="checkbox"]:checked + label::before {
	background-image: url("../images/correct8.png");
	background-image: url("../images/correct8.svg");
	background-position: 50%;
	background-repeat: no-repeat;
}
form [type="submit"] {
	display: inline-block;
	margin-bottom: 1.5em;
	margin-top: 2.5rem;
