.result-details {
	display: flex;
	align-items: stretch;
	justify-content: space-between;
	margin-bottom: 20px;
	padding-top: 15px;
}
.result-details-column {
	width: 31%;
	position: relative;
}
.result-details-column:after {
	content: "";
	position: absolute;
	top: 0;
	left: -5%;
	width: 1px;
	height: 100%;
	background: black;
}
.result-details-column:first-child:after {
	display: none;
}
