@media screen and (max-width: 480px) {
.container {
	margin-right: auto;
	margin-left: auto;
	padding-left: 15px;
	padding-right: 15px;
}
// .usa-search [type="search"],
// .usa-search .usa-search-input {
// 	border-right: 1px solid #000;
// }
.custom-select, .custom-select select {
	width: 200px;
}
.state-select label {
	margin-bottom: 15px;
}
.posted-select, .posted-select select {
	width: 200px;
}
form [type="submit"] {
	display: inline-block;
	margin-bottom: 1.5em;
	margin-top: 2.5rem;
	margin-left: 0px;
}
.ttli {
	height: 58px;
	vertical-align: top;
	padding: 10px 20px 10px 15px;
}
.tooltip .tooltiptext,
.tooltip-left .tooltip-left-text {
	width: 120px;
	margin-left: -60px;
}
[type="checkbox"] + label,
[type="radio"] + label {
	cursor: pointer;
	font-weight: 400;
	margin-bottom: 0.5em;
	text-indent: -41px;
	padding-right: 10px;
}
.setaside {
	line-height: 26px;
}
/*accssible tooltip*/
.visuallyhidden {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}
.showvisuallyhidden {
	clip: auto;
	overflow: visible;
}
.btn-tooltip {
	border: none;
	background: none;
	position: relative;
}
#cntctbx {
	border: 1px solid $contact-center-box-border;
	margin: 0 auto 25px auto;
	width: 100%;
}
/*pagelevel survey*/
//TODO: Move survey styles out of faceted search css file
#cntctbx h2 {
	max-width: 100%;
}

#cntctbx ul{
	margin: 0 0 15px 55px;
}
span.pager-dscrpt {
	display: none;
}
.vocradio input[type='radio'] + label,
.voccheckbox input[type='checkbox'] + label {
	vertical-align: middle;
	display: table-cell;
	padding: 17px 0 0 46px;
}
#pagesurvey-trgt {
	background: $survey-omb-number;
	padding: 0 0 10px 0;
	margin: 0 auto 0 auto;
	max-width: 100%;
}
#pagesurvey-trgt legend {
	margin: 0 0 0 0;/* float: left;*/
}
#pagesurvey-hdr {
	font-size: 130%;
	display: block;
	color: #fff;
	padding: 12px 0 15px 65px;
	margin: 20px auto 0 auto;
	max-width: 100%;
	height: auto;
	background: $survey-header url('/sites/all/themes/usa/images/Page_Survey_Invite_Icon.png') left 20px top 12px no-repeat;
}
#pagesurvey-ombnum {
	margin: 0 0 20px 0;
	padding: 0 0 20px 0;
	background: $survey-omb-number;
	text-align: center;
	max-width: 100%;
	font-size: 90%;
}
.plsurvey {
	margin: auto;
	width: 100%;
}

div.surveyNav {
	color: rgba(0,0,0,0);
	text-decoration: none;
	font-weight: bold;
	font-size: 1em;
	margin: auto;
	padding: 60px 0 10px 0;
	text-align: center;
}
/*page level survey ends*/

.filter-divider {
	margin-left: 0px;
}
label {
	max-width: 60rem;
}
ol.finderlist{
	padding-left: 1.94rem;
}

ul, ol {
	padding-left: 0;
}
ul.pagination > li.pager-dscrpt2 {
	display: inline;
	margin-left: 5px;
	line-height: 17px;
	margin-top: 23px;
}
ul.pagination li.previous, ul.pagination li.next {
	width: auto;
	margin: 15px auto 5px auto;
	display: inline-block;
}

ul.pagination li {
  line-height: 20px; }

article ul li,
article ol li,
ul li {
	line-height: 1.5;
	margin: 15px auto auto auto;
	width: 100%;
	height: auto;
}

article ul li:last-child {
	margin-bottom: 0px;
}
section > .usa-grid {
	padding-right: 0;
	padding-left: 0
}
/*.filter-box {
	border: none;
}*/
.usa-button-primary-alt {
	margin-left: 0px;
	width: 100%;
	background-color: $aqua-darker;
	color: $button-text;
}
/*input, textarea, select {
	width: 94%;
}*/
//TODO: find a better way to override core styles rather than overwriting them
.usa-grid {
	padding-right: 0;
	padding-left: 0;
}
.usa-accordion-button {
	height: auto;
}
// .usa-accordion-content-filter4 {
// 	background-color: $white;
// 	overflow: visible;
// 	padding: 1.5rem;
// 	font-family: Source Sans Pro, Helvetica Neue, Helvetica, Roboto, Arial, sans-serif;
// }
.item-list2 {
	margin-left: -252px; //TODO: Investigate a better way to position this
	margin-top: 50px;
	margin-bottom: -10px;
}
ul.pagination button.left {
	height: 40px;
	background: $aqua-blue;
	margin-top: 0.9em;
	color: $button-text;
	padding-left: 0;
	padding-right: 0;
}
ul.pagination button.left:hover {
	height: 40px;
	background: $aqua-darker;
	margin-top: 0.9em;
	color: $button-text;
	padding-left: 0;
	padding-right: 0;
}
.bttop1 {
	padding: 20px 0 0 0;
	margin-right: 100px;
}
.bttop {
	margin-right: 100px;
}

#cntctbx {
    width: 100%;
}
}
@media screen and (min-width: 391px) and (max-width: 407px){
	.tooltip-left .tooltip-left-text {
		visibility: hidden;
		width: 110px;
		background-color: $dark-gray;
		color: #fff;
		text-align: left;
		border-radius: 5px;
		padding: 5px;
		position: absolute;
		z-index: 1;
		top: 120%;
		left: 55%;
		margin-left: -60px;
		font-size: 90%;
		font-weight: 400;
		text-indent: 0;
		line-height: 1.2;
		word-wrap: break-word;
		height: auto;
	}
	.tooltip-left .tooltip-left-text::after {
		content: "";
		position: absolute;
		bottom: 100%; /* At the top of the tooltip */
		left: 55%;
		margin-left: -5px;
		border-width: 5px;
		border-style: solid;
		border-color: transparent transparent $dark-gray transparent;
		overflow: visible;
	}
}

@media screen and (max-width: 390px) {
	.tooltip-left .tooltip-left-text {
		visibility: hidden;
