@media screen and (max-width: 320px) {
.posted-select, .posted-select select {
		width: 170px;
}

ul.pagination li.previous,
ul.pagination li.next {
	border: 2px $aqua-blue solid;
	border-radius: 0.3rem;
	white-space: nowrap;
	heigh: 41px;
	line-height:31px;
}

.tooltip-left {
	position: relative;
	display: inline-block;
	background-color: $white;
}
.tooltip-left .tooltip-left-text {
	visibility: hidden;
	width: 110px;
	background-color: $dark-gray;
	color: #fff;
	text-align: left;
	border-radius: 5px;
	padding: 5px;
	position: absolute;
	z-index: 1;
	top: 120%;
	left: 128%;
	margin-left: -75px;
	font-size: 90%;
	font-weight: 400;
	text-indent: 0;
	line-height: 1.2;
	word-wrap: break-word;
	height: auto;
}
.tooltip-left .tooltip-left-text::after {
	content: "";
	position: absolute;
	bottom: 100%; /* At the top of the tooltip */
	left: 50%;
	margin-left: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: transparent transparent $dark-gray transparent;
	overflow: visible;

}
}
}
