/***********************
16. Survey
***********************/

  #vocModalOverlay {
      width:100%;
      height:100%;
      z-index:9999; /* places the modal overlay between the main page and the modal dialog*/
      background-color:#000;
      opacity:0.5;
      position:fixed;
      top:0;
      left:0;
      display:none;
      margin:0 0 0 0;
      padding:0 0 0 0;
  }

  div#vocModal {
      width:50%;
      min-width:320px;
      margin: auto auto auto auto;
      padding: 100px 20px 20px 20px;
      text-align: center;
      background-color:#fff;
      border: none;
      z-index:10000; /* places the modal dialog on top of everything else */
      position:fixed;
      top:25%;
      display:none;
  }
  #vocModal:before {
    position:absolute;
    display:block;
    top:0;
    left:0;
    content:"";
    height:80px;
    width:100%;
    background-image:url("../images/Logo_USA_Survey.png");
    background-size:contain;
    background-repeat:no-repeat;
    background-position:center;
    background-color: #FFF;
  }
  html:lang(es) #vocModal:before {
    background-image:url("../images/Logo_Gobierno_Survey.png");
  }

  #vocModal p {
    width:100%;
    max-width:400px;
    margin:auto auto 20px auto;
  }
  #vocModal h1 {
    text-align:center;
  }

  #vocEnter, .vocCancelButton, .start-survey  {
    height:38px;
    font-size:100%;
    font-weight: bold;
    padding: 6px 12px 6px 12px;
    cursor: pointer;
  }
  .vocCancelButton {
    color:#FFF;
    background:$nav-bg;
    border: 1px $nav-bg solid;
  }

  #vocEnter, .start-survey {
    color:$nav-bg;
    background: $aqua-blue;
    border: 1px $aqua-blue solid;
    margin: 0 5px 0 0;
  }

  #vocEnter:hover, .start-survey:hover {
  color: $nav-bg;
  background: $aqua-darker;
  border: 1px $aqua-darker solid;
  margin: 0 5px 0 0; }

  .screen-reader-offscreen {
      position:absolute;
      left:-999px; /* suggestion to change to 10000 to left - todo */
      width:1px;
      height:1px;
      top:auto;
  }


//TODO: These styles are too general and could accidentally affect other styles.
.content {
  margin: 25px 15px 25px 15px;
}
.banner {
  display: block;
  margin: 0 auto 10px auto;
}
.message {
  margin: 15px 0 15px 0;
}
.float-right {
  float: right;
}
.privacy-policy {
  text-decoration: none;
  color: $privacy-policy;
  font-size: .9em; /* suggestion to change to 90% - todo */
}
.start-survey {
  display: block;
  margin: 25px auto 25px auto;
  width: 150px;
  font-size: 1.2em; /* suggestion to change to 120% - todo */
  cursor: pointer;

}
#survey-target {
  display:none;
}
